<template>
  <div class="home">
    <h3>编辑推荐</h3>
    <ul class="card-list">
      <HomeCard v-for="item in personalizeds" :key="item.id" :item="item" @card-item="$emit('card-item',$event)"/>
    </ul>
    <h3>最新音乐</h3>
     <div class="loading" v-show="loading">
      <img src="../img/loading.gif" alt="">
    </div>
    <ul class="songItem">
      <SongItem v-for="song in newsongs" :key="song.id" :song="song"  
      @update-song="songitemapp($event)"
       :songId="songId"
      :playing="playing"/>
    </ul>
  </div>
</template>

<script>
// @ is an alias to /src
import HomeCard from "@/components/HomeCard.vue";
import SongItem from "@/components/SongItem.vue";
export default {
  name: "HomeView",
  components: {
    HomeCard,
    SongItem,
  },
  props:{
     songId: [String, Number],
    playing:Boolean
  },
  data: function () {
    return {
      personalizeds: [],
      newsongs: [],
      loading:false,
    };
  },
  methods:{
songitemapp:function (event) {
  this.$emit('update-song',event);
  this.$emit('update-songhome', this.newsongs)
// console.log(event,this.newsongs);
}
  },
  created() {
    this.loading=true;
   
    Promise.all([
      this.axios.get("https://apis.netstart.cn/music/personalized", {
        params: { limit: 6 },
      }),
      this.axios.get("https://apis.netstart.cn/music/personalized/newsong"),
    ]).then(([p, n]) => {
        this.loading=false;
      this.personalizeds = p.data.result;
      this.newsongs = n.data.result;
      // console.log(p.data.result, n.data.result);
    });
  },
};
</script>
