<template>
  <div class="playbar">
    <div class="bg-rr">
      <img
        :class="{ playing: playing }"
        :src="
          song
            ? `${
                song.picUrl? 
                song?.picUrl : song?.al?.picUrl
              }?imageView=1&type=webp&thumbnail=50x0`
            : ''
        "
        alt=""
      />
    </div>
    <h3>
      {{
        song.song?.artists[0]?.name
          ? song.song?.artists[0]?.name
          : song.ar[0]?.name
      }}
      - {{ song.song?.album.name ?
      song.song?.album.name : song.al.name}}
    </h3>
    <div class="p-yuan">
      <canvas ref="canvas" width="40" height="40"></canvas>
      <button v-if="!playing" @click.stop="$emit('song-play')">
        <i class="fa fa-play"></i>
      </button>
      <button v-else @click.stop="$emit('song-pause')">
        <i class="fa fa-pause"></i>
      </button>
    </div>
    <button @click.stop="$emit('toggle-showPlayList')">
      <i class="fa fa-th-list"></i>
    </button>
  </div>
</template>

<script>
export default {
  props: {
    song: Object,
    playing: Boolean,
    duration: Number,
    currentTime: Number,
  },
  computed: {
    persontage: function () {
      return this.currentTime / this.duration;
    },
  },

  watch: {
    persontage: function (n) {
      // console.log(n);

      var canvas = this.$refs?.canvas;
      var context = canvas.getContext("2d");

      context.clearRect(0, 0, 40, 40);

      context.beginPath();
      context.arc(20, 20, 16, -0.5 * Math.PI, Math.PI * (2 * n - 0.5));
      // context.closePath();
      context.lineWidth = 2;

      context.strokeStyle = "#fff";
      context.stroke();
    },
  },
};
</script>

<style lang="less" scoped>
// canvas {
//   box-shadow: 0 0 3px 0 red;
// }
@keyframes rotate {
  form {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.playbar {
  width: 100%;
  height: 80rem;
  background-color: rgb(83, 82, 81);
  border-radius: 5rem 5rem 0 0;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 0 10rem;
  z-index: 999;

  h3 {
    width: 200rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-size: 14rem;
    color: #fff;
    text-align: center;
  }
  .bg-rr {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 75rem;
    height: 75rem;
    position: relative;
    top: -20rem;
    left: 1rem;
    img {
      border-radius: 50%;
      animation: rotate 8s linear infinite;
      animation-play-state: paused;

      &.playing {
        animation-play-state: running;
      }
    }

    background-image: url(../img/rr.png);
    background-repeat: no-repeat;
    background-size: contain;
  }

  .p-yuan {
    width: 40rem;
    height: 40rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    button {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      background: transparent;
      border: none;
      outline: none;
      background-color: rgba(248, 250, 251, 0.245);
      display: flex;
      justify-content: center;
      align-items: center;
      i {
        font-size: 18rem;
      }
    }
  }
  button {
    border: none;
    border-radius: 50%;
    display: inline-block;
    width: 30rem;
    height: 30rem;
    background-color: rgba(248, 250, 251, 0.245);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10rem;
    i {
      font-size: 20rem;
    }
  }
}
</style>