<template>
  <div class="search">
    <div class="m-input">
      <div class="inputcover">
        <!-- autocomplete="off" -->
        <i class="sc-svg"></i>
        <input
          type="text"
          placeholder="搜索歌曲、歌手、专辑"
          v-model="text"
          @keyup.enter="addHistory(text)"
          @focus="searchRes = null;"
          @keyup.delete="searchRes = null;"
        />
        <!-- <input type="text"  > -->
        <figure class="close"><i class="x-svg" @click="deleteText"></i></figure>
      </div>
    </div>
    <div class="loading" v-show="loading">
      <img src="../img/loading.gif" alt="">
    </div>
    <div class="hotsearch" v-show="!text.length">
      <h5>热门搜索</h5>
      <ul class="list" v-if="hotsearch">
        <HotSearch
          v-for="(hotw, index) in hotsearch"
          :key="index"
          :hotw="hotw"
          @gethot-w="getHotW"
        />
      </ul>
      <ul class="history">
        <SearchHistory
          v-for="(item, i) in history"
          :key="i"
          :item="item"
          @d-h="deleteHistory"
          @get-history="getHistory"
        />
      </ul>
    </div>

    <div class="searchitem" v-show="text.length && !searchRes" v-if="search" >
      
      <h4>搜索“{{ text }}”</h4>
      <ul>
        <SearchItem
          v-for="(result, index) in search"
          :key="index"
          :result="result"
          @get-keyword="getKeyword"
          
        />
      </ul>
    </div>
    <!-- <li v-for="(o, i) in searchRes?.songs" :key="i">{{ o.name }}</li> -->
    <section class="search" v-if="searchRes && text.length">
      <SearchIcon :artist="artist" :album="album" @artists-view="$emit('artists-view',$event)"/>
      <SearchRes
        v-for="song in searchRes"
        :key="song.id"
        :song="song"
          @update-song-search="songitemapp($event)"
           :songId="songId"
      :playing="playing"
      />
    </section>
  </div>
</template>

<script>
import SearchItem from "@/components/SearchItem.vue";
import SearchHistory from "@/components/SearchHistory.vue";
import HotSearch from "@/components/HotSearch.vue";
import SearchIcon from "@/components/SearchIcon.vue";
import SearchRes from "@/components/SearchRes.vue";
var _ = require("lodash");
export default {
  data: function () {
    return {
      search: {},
      text: [],
      scshow: false,
      hotsearch: {},
      history: JSON.parse(window.localStorage.getItem("history")) || [],
      searchRes: null,
     artist: {},
      album: {},
      loading:false,
    };
  },
  props:{
 songId: [String, Number],
    playing:Boolean
  },
  components: {
    SearchItem,
    HotSearch,
    SearchHistory,
    SearchIcon,
    SearchRes,
  },
  created() {
     this.loading=true;
    this.axios.get("https://apis.netstart.cn/music/search/hot").then((res) => {
      // console.log(res.data.result);
      this.hotsearch = res.data.result.hots;
       this.loading=false;
    });
  },
//   computed:{
// text:function () {
  
// }
  // },
  methods: {
    
    songitemapp:function(event){
 this.$emit('update-song-search',event);
  // this.$emit('update-songhome', this.search);
  // console.log(event);
    },
    deleteText: function () {
      this.text = "";
    
       this.scshow = true;
    },
    getHotW: function (w) {
      this.text = w;
      this.getsearchresult(w);
      // console.log("kk");
    },

    getsearchresult: function (newText) {
       this.loading = true;
       this.scshow= false;
      this.history = [...new Set([newText, ...this.history])];
      return Promise.all([
        this.axios.get("https://apis.netstart.cn/music/search", {
          params: { keywords: newText, limit: 10, offset: 0,type:1018},
        }),
        this.axios.get("https://apis.netstart.cn/music/search/multimatch", {
          params: { keywords: newText },
        }),
      ]).then(([res, n]) => {
        // console.log(123,res.data.result.song.songs);
         this.loading = false;
        this.searchRes = res.data.result.song.songs;
        this.artist=n.data.result.artist;
        this.album = n.data.result.album;
      });
    },
    getHistory: function (h) {
      this.text = h;
      this.getsearchresult(h);

    },
    getKeyword: function (keyw) {
      this.text = keyw;
      this.getsearchresult(keyw);
     
    },
    addHistory: function (tt) {
      console.log("tt");
      if (tt) {
        this.getsearchresult(tt);
      }
      
    },
    deleteHistory: function (d) {
      // console.log(d);
      this.history = this.history.filter((o) => o !== d);
    },
  },
// computed:{
// textt:function (tt) {
//   console.log(tt);
//   return tt;
// }
// },
  watch: {
    text: _.debounce(function (newText) {
    //  console.log(newText);
   
      if (newText) {
        // this.history = [...new Set([newText, ...this.history])];
        // this.scshow = true;
      this.loading=true;
        this.axios
          .get("https://apis.netstart.cn/music/search/suggest?type=mobile", {
            params: { keywords: newText },
          })
          .then((res) => {
            this.search = res?.data?.result?.allMatch;
             this.loading = false;
          });
      } else {
        this.scshow = false;
           
      }
        // this.searchRes=null;
          this.scshow = true;
    }, 500),
    history: function (newHistory) {
      localStorage.setItem("history", JSON.stringify(newHistory));
    },
  },
};
</script>

<style lang="less" scoped>
.search {
  .loading{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
    img{
      width: 40rem;
      height: 40rem;
    }
  }
  .m-input {
    padding: 15rem 10rem;
    box-shadow: 0 0 2rem 0px rgb(208, 208, 208);
    .inputcover {
      position: relative;
      width: 100%;
      height: 30rem;
      padding: 0 30rem;
      box-sizing: border-box;
      background: #ebecec;
      border-radius: 30rem;

      .sc-svg {
        display: inline-block;
        width: 13rem;
        height: 13rem;
        background-image: url(../img/sc.svg);

        position: absolute;
        left: 0;
        top: 9rem;
        margin: 0 8rem;
        vertical-align: middle;
      }
      input {
        width: 100%;
        height: 30rem;
        line-height: 18rem;
        background: rgba(0, 0, 0, 0);
        font-size: 14rem;
        color: #333;
        border: none;
        outline: none;
      }
    }

    .close {
      position: absolute;
      right: 0;
      top: 0;
      width: 30rem;
      height: 30rem;
      line-height: 30rem;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;
      .x-svg {
        display: inline-block;
        width: 13rem;
        height: 13rem;
        background-image: url(../img/x.svg);
      }
    }
  }
  h4 {
    height: 50px;
    padding-left: 10px;
    padding-right: 10px;
    box-sizing: border-box;
    font-size: 15rem;
    line-height: 50rem;
    color: #507daf;
    border-bottom: 1rem solid rgba(208, 208, 208, 0.43);
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .hotsearch {
    padding: 15px 10px 0;
    h5 {
      font-size: 12rem;
      line-height: 12rem;
      color: #666;
    }
    .list {
      margin: 10px 0 7px;
    }
  }
}
</style>