<template>
  <li class="playcom">
    <img
      class="u-pic"
      v-if="playcom"
      :src="`${playcom.user.avatarUrl}?imageView=1&type=webp&thumbnail=61x0`"
      alt=""
    />
    <div class="c-top">
      <div class="c-fl">
        <div class="c-title">
          <div class="vvip">
            <h4>{{ playcom.user.nickname }}</h4>
            <span
              v-if="playcom.user.vipRights?.redVipLevel != 0"
              class="v-icon"
            >
              <img
                v-if="playcom.user.vipRights?.associator?.iconUrl"
                :src="`${playcom?.user?.vipRights?.associator.iconUrl}?imageView=1&type=webp&thumbnail=27x0`"
                alt=""
              />
            </span>
          </div>
          <h5>
            {{
              playcom.timeStr.split("-")[0] +
              "年" +
              playcom.timeStr.split("-")[1] +
              "月" +
              playcom.timeStr.split("-")[2] +
              "日"
            }}
          </h5>
        </div>
        <p>{{playcom.likedCount}} <i class="fa fa-thumbs-o-up" @click="colo" :style="{color:colred?'red':''}"></i></p>
      </div>

      <span class="v-con">{{ playcom.content }}</span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    playcom: Object,
  },
  data:function () {
    return{
      colred:false,
    }
  },
  methods:{
    colo:function () {
      if (this.colred===false) {
         this.colred=true;
     this.$emit("playcom-content-add",this.playcom.commentId)
      }else{
        this.colred=false;
        this.$emit("playcom-content-jj",this.playcom.commentId)
      }
     
    }
  },
 
};
</script>

<style lang="less" scoped>
.playcom {
  display: flex;
  padding-top: 10rem;
  img.u-pic {
    display: block;
    border-radius: 50%;
    width: 30rem;
    height: 30rem;
    margin: 0 10rem;
  }
  .c-top {
    width: 100%;
    padding-right: 10rem;
    padding-bottom: 11rem;
    border-bottom: 1px solid rgba(190, 187, 187, 0.521);
    .c-fl {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .c-title {
        .vvip {
          display: flex;
          align-items: center;
          h4 {
            font-size: 14rem;
            color: #666;
            line-height: 20rem;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          span.v-icon {
            height: 12rem;
            display: inline-block;
            margin: 0 4rem;
          }
        }

        h5 {
          font-size: 9rem;
          color: #999;
        }
      }
      p {
        display: inline-block;
        min-width: 30rem;
        padding-left: 5rem;
      }
    }
    .v-con {
      color: #333;
      font-size: 15rem;
      line-height: 22rem;
      margin-top: 5rem;
      display: inline-block;
    }
  }
}
</style>