<template>
  <section>
    <div class="box-head">
      <div
        class="head-bg"
        v-if="playlist?.coverImgUrl"
        :style="`background-image: url('${playlist?.coverImgUrl}?imageView=1&type=webp&thumbnail=150x0')`"
      ></div>
      <header>
          <i class="fa fa-chevron-left" @click="$router.back()"></i>
        <div class="h-fl"  v-if="playlist">
          <img
            v-if="playlist"
            :src="`${playlist?.coverImgUrl}?imageView=1&type=webp&thumbnail=253x0`"
            alt=""
          />
          <span>歌单</span>
          <div class="ii" v-if="playlist">
            <i></i>
            <p
              v-text="
                playlist?.playCount > 100000000
                  ? (playlist?.playCount / 100000000).toFixed(1) + '亿'
                  : (playlist?.playCount / 10000).toFixed(1) + '万'
              "
            ></p>
          </div>
        </div>
        <div class="h-fr"  v-if="playlist">
          <h2>{{ playlist?.name }}</h2>
          <div class="title">
            <img
              v-if="playlist"
              :src="`${playlist?.creator?.avatarUrl}?imageView=1&type=webp&thumbnail=61x0`"
              alt=""
            />
            <span></span>
            {{ playlist?.creator.nickname }}
          </div>
        </div>
      </header>
    </div>
    <section class="s-list">
      <p>歌曲列表</p>
      <ul>
        <PlayList
          v-for="(playsong, item) in playsongs"
          :key="item"
          :item="{ item, playsong }"
          :songId="songId"
          @update-song="songitemapp($event)"
          :playing="playing"
        />
      </ul>
    </section>
    <section class="s-coms">
      <p>精彩评论</p>
      <ul>
        <PlayComs
          v-for="playcom in playcoms"
          :key="playcom.commentId"
          :playcom="playcom"
        />
      </ul>
    </section>
    <div class="loading" v-if="!playlist">
      <img src="../img/loading.gif" alt="" />
    </div>
  </section>
</template>

<script>
import PlayList from "@/components/PlayList.vue";
import PlayComs from "@/components/PlayComs.vue";
export default {
  components: {
    PlayList,
    PlayComs,
  },
  data: function () {
    return {
      playlist: null,
      playsongs: null,
      playcoms: null,
      nuecontent: 0,
    };
  },
  props: {
    songId: [Number, String],
    playing: Boolean,
  },
  methods: {
    songitemapp: function (event) {
      this.$emit("update-song", event);
      this.$emit("update-songhome", this.playsongs);
      // console.log(event,this.newsongs);
    },
  },
  created() {
    // console.log(123);
    // console.log(this.$route.query.id);
    Promise.all([
      this.axios.get(
        "https://apis.netstart.cn/music/playlist/detail/dynameic",
        {
          params: { id: this.$route.query.id },
        }
      ),
      this.axios.get("https://apis.netstart.cn/music/playlist/track/all", {
        params: { id: this.$route.query.id, limit: 20, offset: 1 },
      }),
    ]).then(([res, res2]) => {
      // console.log(res, res2);
      this.playlist = res.data.playlist;
      this.playsongs = res2.data.songs;
    });
    this.axios
      .get("https://apis.netstart.cn/music/comment/playlist", {
        params: { id: this.$route.query.id },
      })
      .then((res) => {
        this.playcoms = res.data.hotComments;
      });
  },
};
</script>

