<template>
  <li class="p-li"
    @click="songitemapp">
    <div class="p-fl">
      <span>{{ item.item + 1 }}</span>
      <div class="p-text">
        <h3>{{ item.playsong?.name }}</h3>
        <h5>{{ item.playsong?.ar[0].name }} - {{ item.playsong?.al.name }}</h5>
      </div>
    </div>
     <p class="newsong-play" v-if="item.playsong.id != songId"></p>
    <div v-else class="newsong-play active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
     songId: [Number, String],
     playing: Boolean,
  },
    methods:{
    songitemapp:function () {
      this.$emit('update-song', this.item.playsong);
      // console.log(this.item.playsong);
    }
  }
};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
  from {
    transform: rotateX(5deg);
  }
  to {
    transform: rotateX(80deg);
  }
}
.p-li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 10rem;
  .p-fl {
    display: flex;
    align-items: center;
    // flex: 1 1 auto;
    // padding: 6rem 0;
   
    span {
      width: 40rem;
      font-size: 17rem;
      color: #999;
      text-align: center;
      height: 55rem;
      display: flex;
    align-items: center;
    justify-content: center;
    }
    .p-text {
         border-bottom: 1rem solid rgba(128, 128, 128, 0.305);
         padding: 6rem 0;
      h3 {
        width: 300rem;
        font-size: 17rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        color: #333;
      }
      h5 {
        font-size: 12rem;
        color: #888;
         overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
        width: 300rem;
      }
    }
  }
  p {
    // float: right;
    width: 22rem;
    height: 22rem;
    background-position: -24rem 0;
    background-size: 166rem 97rem;
    background-image: url(../img/index_icon_2x.png);
  }
   .newsong-play {
    width: 22rem;
    height: 22rem;
    background-position: -24rem 0;
    background-size: 166rem 97rem;
    background-image: url(../img/index_icon_2x.png);

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 80%;
        border-radius: 5rem;
        background: linear-gradient(to bottom, #1ee95d, #ce1417 70%);
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>