<template>
  <footer class="fft">
    <div class="range">
      <span
        >{{
          Math.floor(tmpCurrentTime / 60)
            .toString()
            .padStart(2, "0")
        }}:{{
          parseInt(tmpCurrentTime % 60)
            .toString()
            .padStart(2, "0")
        }}</span
      >
      <div class="p-bur">
        <input
          type="range"
          name=""
          id=""
          min="0"
          step="0.01"
          :max="duration"
          :value="tmpCurrentTime"
          @input="$emit('chang-draging', (draging = true)); changevalue($event.target.value)"

          @change="
            draging = false;
            changevalue($event.target.value)
           
            
          "
        />
        <p :style="{ width: (tmpCurrentTime / duration) * 100 + '%' }" ref="pp"></p>
      </div>
      <span
        >{{
          Math.floor(duration / 60)
            .toString()
            .padStart(2, "0")
        }}:{{
          parseInt(duration % 60)
            .toString()
            .padStart(2, "0")
        }}</span
      >
    </div>
    <div class="maskm" ref="mask">{{maskm}}</div>
    <div class="ctrl">
      <button @click="$emit('change-model');mol(model)">
        <i :class="`fa ${model}`"></i>
      </button>
      <!-- <button><i class="fa fa-random"></i></button> -->
      <!-- fa-exchange  fa-refresh-->
      <button @click="$emit('change-song', 'prev')">
        <i class="fa fa-step-backward"></i>
      </button>
      <button v-if="!playing" @click.stop="$emit('song-play')">
        <i class="fa fa-play"></i>
      </button>
      <button v-else @click.stop="$emit('song-pause')">
        <i class="fa fa-pause"></i>
      </button>
      <button @click="$emit('change-song', 'next')">
        <i class="fa fa-step-forward"></i>
      </button>
      <!-- <button><i class="fa fa-star"></i></button> -->
      <button @click.stop="$emit('toggle-showPlayList')">
        <i class="fa fa-th-list"></i>
      </button>
    </div>
  </footer>
</template>

<script>
export default {
  props: {
    tmpCurrentTime: [String, Number],
    duration: Number,
    playing: Boolean,
    // draging: Boolean,
    model: String,
    currentTime: [Number, String],
  },
  data: function () {
    return {
      draging: false,
    maskMs:["列表循环","随机播放","单曲循环"],
    maskm:"列表循环"
    };
    
  },
  methods:{
    mol:function (m) {
      // console.log(m);
      if (m=="fa-random") {
        // ["fa-random", "fa-exchange", "fa-refresh"],
        this.maskm=this.maskMs[1]
      }else
      if(m=="fa-exchange"){
         this.maskm=this.maskMs[2]
      }else
      if(m=="fa-refresh"){
         this.maskm=this.maskMs[0]
      }
     var temp= this.$refs.mask;
       this.$refs.mask.style.opacity="1";
    //  console.log(this.$refs.mask);
      setTimeout(function () {
       temp.style.opacity="0"
      },1000)
    },
    changevalue:function (e) {
      // console.log(e);

      this.$refs.pp.style.width=e/this.duration * 100+"%"
    
       this.$emit('update-time', e);
      this.$emit('chang-draging', (this.draging = false));
    }
  }
};
</script>

<style lang="less" scoped>
.fft {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 80rem;
  padding-bottom: 40rem;
  //   background-color: #f9f5f4;
  margin-bottom: 30rem;
  .range {
    width: 250rem;
    margin: 10rem 0;
    height: 4rem;
    display: flex;
    justify-content: center;
    align-items: center;
  padding-bottom: 20rem;
    position: relative;

    span {
      color: #fff;
      font-size: 12rem;
      margin: 0 5rem;
    }
    .p-bur {
      width: 250rem;
      height: 4rem;
      position: relative;
       background-color: rgba(163, 159, 159, 0.982);
           border-radius: 10rem;
      input {
        width: 100%;
        height: 100%;
        opacity: 0;
        position: absolute;
        top: 0;
        left: 0;
      }
      p {
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: rgb(239, 233, 233);
        top: 0;
        left: 0;
        border-radius: 10rem;
      }
    }
  }
.maskm{
  opacity: 0;
  font-size: 12rem;
  color: #fff;
  background-color: rgba(49, 49, 48, 0.477);
  padding: 10rem;
  top: 50%;
  border-radius: 5rem;
  position: absolute;
}
  .ctrl {
    width: 250rem;
    display: flex;
    justify-content: center;
    button {
      width: 30rem;
      height: 30rem;
      border: 0;
      outline: 0;
      border-radius: 5rem;
      &:nth-child(2n) {
        margin: 0 10rem;
      }
      i {
        font-size: 16rem;
      }
    }
  }
}
</style>