<template>
 
  <li
    class="song-item"
   @click="songitemapp"
    :class="{ active: song.id === songId }"
  >
    <div class="people">
      <div class="song-name">{{ song.name }}</div>
      <div class="text">
        <i></i>{{ song.song.artists[0].name }} - {{ song.song.album.name }}
      </div>
    </div>
    <p class="newsong-play" v-if="song.id != songId"></p>
    <div v-else class="newsong-play active" :class="{ playing: playing }">
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  </li>
</template>

<script>
export default {
  props: {
    song: Object,
    songId: [Number, String],
    playing: Boolean,
  },
  methods:{
    songitemapp:function () {
      this.$emit('update-song', this.song);
      // console.log(this.song);
    }
  }
};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
  from {
    transform: rotateX(5deg);
  }
  to {
    transform: rotateX(80deg);
  }
}

.song-item {
  padding: 6rem 10rem;
  border-bottom: 1rem solid rgba(150, 145, 145, 0.168);
  display: flex;
  justify-content: space-between;
  align-items: center;
  .people {
    .song-name {
      font-size: 17rem;
        overflow: hidden;
         text-overflow: ellipsis;
      white-space: nowrap;
      width: 300rem;
    }
    .text {
      font-size: 12rem;
      color: #888;
        overflow: hidden;
         text-overflow: ellipsis;
      white-space: nowrap;
      width: 300rem;
      i {
        display: inline-block;
        width: 12rem;
        height: 8rem;
        background-image: url(../img/index_icon_2x.png);
        margin-right: 4rem;
        background-size: 166rem 97rem;
      }
    }
  }
  .newsong-play {
    width: 22rem;
    height: 22rem;
    background-position: -24rem 0;
    background-size: 166rem 97rem;
    background-image: url(../img/index_icon_2x.png);

    &.active {
      background-image: none;
      display: flex;
      justify-content: space-around;
      span {
        width: 11%;
        height: 80%;
        border-radius: 5rem;
        background: linear-gradient(to bottom, #1ee95d, #ce1417 70%);
        animation: playingrunning 0.8s infinite alternate;
        animation-play-state: paused;
        transform-origin: bottom;
        &:nth-child(1) {
          animation-delay: 0s;
        }
        &:nth-child(2) {
          animation-delay: -0.2s;
        }
        &:nth-child(3) {
          animation-delay: -0.4s;
        }
        &:nth-child(4) {
          animation-delay: -0.6s;
        }
      }
      &.playing {
        span {
          animation-play-state: running;
        }
      }
    }
  }
}
</style>