<template>
  <section>
    <header>
      <div
        class="artists-pic" v-if="artistsView?.artist?.cover"
        :style="`background-image:url('${artistsView?.artist?.cover}?imageView=1&type=webp&thumbnail=500x0')`"
      ></div>
      <div class="jianjie-text">
        <i class="fa fa-chevron-left" @click="$router.back()"></i>
        <div class="text">
          <h4>{{ artistsView?.artist?.name }}</h4>
          <p v-if="artistsView?.artist?.rank">
            歌手榜华语地区 No.{{ artistsView?.artist?.rank?.rank }}
          </p>
          <span v-if="artistsView?.artist?.identifyTag" class="identifyTag">{{
            artistsView?.artist?.identifyTag[0]
          }}</span>
          <span
            class="identities"
            v-for="i in artistsView?.secondaryExpertIdentiy"
            :key="i.expertIdentiyName "
            >{{ i.expertIdentiyName }}</span
          >
        </div>
      </div>
    </header>
     <div class="loading" v-show="loading">
      <img src="../img/loading.gif" alt="">
    </div>
    <div class="jianjie">
      <h3 v-show="!loading">歌手简介</h3>
      <p ref="jianjiep">{{ artistsView?.artist?.briefDesc }}</p>
      <div id="showp" @click="showp" v-show="showpp&&!loading" >
        完整歌手介绍 <i class="fa fa-angle-down"></i>
      </div>
      <div id="showp" @click="hidep" v-show="!showpp">
        收起<i class="fa fa-angle-up"></i>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  props: {
    artistsId:Number,
  },
  data: function () {
    return {
      artistsView:null,
      showpp: true,
      loading:false,
    };
  },
created(){
     this.loading=true;
    this.axios
      .get("https://apis.netstart.cn/music/artist/detail", {
        params: { id: this.artistsId },
      })
      .then((res) => {
        // console.log(res.data.data);
        this.artistsView = res.data.data;
        this.loading=false;
      });
 },
  methods: {
    showp: function () {
      this.$refs.jianjiep.style.display = "block";
      this.showpp = false;
    },
    hidep: function () {
      this.$refs.jianjiep.style.display = "-webkit-box";
      this.showpp = true;
    },
  },
};
</script>

<style lang="less" scoped>
header {
  width: 100%;
  height: 300rem;
  background-size: cover;
  background-repeat: no-repeat;
  position: relative;
  .artists-pic {
    width: 100%;
    height: 300rem;
    background-size: cover;
    background-repeat: no-repeat;
    filter: brightness(0.8);
  }
  .jianjie-text {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    padding: 15rem;
    width: 100%;
    height: 100%;
    i {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;
      padding: 15rem;
      font-size: 14rem;
      color: #fff;
    }
    .text {
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;
      padding: 15rem;

      h4 {
        font-size: 16rem;
        color: #fff;
      }
      p {
        font-size: 14rem;
        color: #fff;
        margin-top: 5rem;
      }
      .identifyTag {
        font-size: 12rem;
        color: #fff;
      }
      .identities {
        border: 1rem solid rgba(221, 218, 218, 0.655);
        font-size: 5rem;
        border-radius: 2rem;
        color: rgb(221, 214, 214);
        margin-right: 5rem;
        margin-top: 5rem;
        display: inline-block;
      }
    }
  }
}
.loading{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
    img{
      width: 40rem;
      height: 40rem;
    }
  }
.jianjie {
  width: 100%;
  padding: 15rem;
  h3 {
    font-weight: 700;
    font-size: 16rem;
    margin-bottom: 15rem;
  }

  p {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    line-height: 20rem;
    font-size: 12rem;
  }
  #showp {
    font-size: 12rem;
    width: 100rem;
    height: 20rem;
    text-align: center;
    margin: 15rem auto;
  }
}
</style>