<template>
  <div class="playlistc">
    <ul class="card">
      <p class="card-count">
        当前播放 <span>{{ "(" + songlist.length + ")" }}</span>
      </p>
      <li
        v-for="(songli, index) in songlist"
        :key="index"
        :style="{ color: songli.id === songId ? 'rgb(190, 200, 100)' : '' }"
        @click.stop="$emit('update-song', songli)"
      >
        <div class="card-fl">
          <div
            class="newsong-play active"
            :class="{ playing: playing }"
            v-show="songli.id === songId"
          >
            <span></span>
            <span></span>
            <span></span>
            <span></span>
          </div>
          {{ songli.name }} -
          <span class="aral"
            :style="{ color: songli.id === songId ? 'rgb(190, 200, 100)' : '' }"
            >{{
              songli.song?.artists[0].name
                ? songli.song?.artists[0].name
                : songli.ar[0].name
            }}</span
          >
        </div>
        <i
          class="fa fa-times"
          @click.stop="$emit('card-delete', songli.id)"
        ></i>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    songlist: [Array, Object],
    songId: [Number, String],
    playing: Boolean,
  },
};
</script>

<style lang="less" scoped>
@keyframes playingrunning {
  from {
    transform: rotateX(15deg);
  }
  to {
    transform: rotateX(80deg);
  }
}
.playlistc {
  width: 100%;
  // height: 200rem;
  position: fixed;
  bottom: 100rem;
  left: 0;
  z-index: 999;
  padding: 15rem 20rem;
  .card {
    background-color: rgba(11, 11, 11, 0.721);
    // min-height: calc(100rem- 30rem);
    border-radius: 15rem;
    padding: 10rem;
    width: 100%;
    height: 200rem;
    overflow: auto;
    p.card-count {
      color: rgb(233, 230, 230);
      height: 30rem;
      width: 290rem;
      border-bottom: 1rem solid gray;
      font-size: 12rem;
      display: flex;
      align-items: center;
      margin: auto;
      span{
        font-size: 10rem;
      }
    }
    li {
      border-bottom: 1rem solid gray;
      height: 30rem;
      width: 290rem;
      color: #fff;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: space-between;
      font-size: 14rem;
      text-overflow: ellipsis;
             white-space: nowrap;
              overflow: hidden;
  
      i {
        font-size: 12rem;
      }
      .card-fl {
        height: 100%;
        display: flex;
        align-items: center;
        overflow: hidden;
        span.aral{
          font-size: 10rem;
          color: rgb(223, 219, 219);
        }
        .newsong-play {
          width: 15rem;
          height: 20rem;
          background-position: -24rem 0;
          background-size: 166rem 97rem;
          background-image: url(../img/index_icon_2x.png);
          margin-right: 8rem;
          &.active {
            background-image: none;
            display: flex;
            justify-content: space-around;
            align-items: center;
            span {
              // width: 11%;
              width: 15rem;
              height: 50%;
              border-radius: 15rem;
              background: linear-gradient(to bottom, blue 20%,#1ee95d 50%,#ce1417);
              animation: playingrunning 0.8s infinite alternate;
              animation-play-state: paused;
              transform-origin: bottom;
              &:nth-child(1) {
                animation-delay: 0s;
              }
              &:nth-child(2) {
                animation-delay: -0.2s;
              }
              &:nth-child(3) {
                animation-delay: -0.4s;
              }
              &:nth-child(4) {
                animation-delay: -0.6s;
              }
            }
            &.playing {
              span {
                animation-play-state: running;
              }
            }
          }
        }
      }
    }
  }
}
</style>